body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 1.5rem;
  color: #6b7280;
  line-height: 1.8;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  text-align: center;
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

/** Media Queries **/
.inicio {
  background-image: url("../images/logo.jpeg");
  max-width: 100%;
}

.header {
  background-color: rgb(35, 35, 35);
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}

.cabecera a {
  color: azure;
  text-decoration: none;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  align-items: center;
}
@media (min-width: 768px) {
  .cabecera .logo {
    margin-left: 1rem;
    margin-top: 4rem;
    width: 20rem;
  }
}
@media (min-width: 320px) {
  .cabecera .navbar {
    display: none;
  }
}
@media (min-width: 768px) {
  .cabecera .navbar {
    font-size: 2.3rem;
    padding: 0rem;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (min-width: 768px) {
  .cabecera .menu1 {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 320px) {
  .cabecera .menu2 {
    display: none;
  }
}
@media (min-width: 768px) {
  .cabecera .menu2 {
    font-size: 1.5rem;
    margin-top: 2rem;
    margin: 0 auto;
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(4, 1fr);
    color: white;
  }
  .cabecera .menu2 a {
    font-size: auto;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    margin-right: 1rem;
  }
}
@media (min-width: 768px) {
  .cabecera .categories {
    background: rgb(35, 35, 35);
    padding: 2rem;
    color: rgb(255, 255, 255);
    border-radius: 0.2rem;
    margin-top: 1rem;
    transition: 0.5s;
  }
  .cabecera .categories:hover {
    box-shadow: 0 0 20px goldenrod;
    transform: scale(1.1);
  }
}
.cabecera .center {
  width: 60vh;
  margin-left: 37%;
}

.del {
  display: none;
}

.carrusel {
  margin-top: 3rem;
}
.carrusel p.centrado {
  text-align: center;
}

.products {
  margin-top: 3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .products {
    font-size: 2rem;
  }
}

.container-products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container-products {
    width: 100%;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.producto {
  width: 25rem;
  align-items: center;
  text-decoration: none;
  color: black;
  height: 38rem;
  padding: 1.5rem;
}
.producto .mini-desc {
  font-size: 1.3rem;
  align-items: center;
}
.producto .mini-desc .cart-b {
  background-color: goldenrod;
  padding: 1rem;
  border-radius: 1rem;
  color: white;
  font-size: 1.3rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .producto .mini-desc .cart-b {
    width: 11rem;
    margin-left: 5.4rem;
  }
}
.producto .mini-desc .cart-b:hover {
  background-color: #ae8319;
  cursor: pointer;
}
.producto .mini-desc img.proto {
  width: 220px;
  height: 137px;
}

.product {
  margin-top: 8rem;
}
@media (min-width: 768px) {
  .product {
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.product .name-desc h3 {
  font-size: 2.5rem;
}
.product .final {
  width: 30vh;
  box-shadow: 0 0 3px rgb(112, 112, 112);
  border-radius: 0.7rem;
  padding: 3rem;
  margin: 0 0 0 0;
}
.product .img {
  margin-left: 2rem;
  margin-right: 0;
  padding: 0;
  width: 60vh;
}

.foot {
  background-color: rgb(38, 38, 38);
  color: #fff;
  margin-top: 3rem;
}
@media (min-width: 320px) {
  .foot {
    display: block;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .foot {
    font-size: 1.4rem;
  }
}
.foot a {
  color: #fff;
  text-decoration: none;
}
@media (min-width: 768px) {
  .foot #h4 {
    font-width: bold;
    font-size: 1.5rem;
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    margin-left: 3rem;
  }
}
.foot .menu-footer {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .foot .menu-footer {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    margin-left: 3rem;
  }
}
@media (min-width: 320px) {
  .foot .menu-footer .column-f1 {
    display: block;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .foot .menu-footer .column-f1 {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.foot .menu-footer .newsletter {
  display: block;
}
.foot .iconos {
  margin-top: 3rem;
  padding: auto;
}
.foot .iconos .icons {
  display: block;
}
.foot .iconos .icons .icon {
  width: 3rem;
}
@media (min-width: 768px) {
  .foot .iconos {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.content {
  background: fixed;
